import {__} from '@wordpress/i18n';

import {
    PanelBody,
    SelectControl,
    RangeControl,
    TextControl,
    Dashicon
} from '@wordpress/components';

import {
    InnerBlocks,
    PanelColorSettings,
    InspectorControls,
    useBlockProps
} from '@wordpress/block-editor';

export default function Edit(props) {

    const {attributes, setAttributes} = props;

    const setAttribute = (name, value) => {
        setAttributes({[name]: value});
    };

    const {
        icon,
        emoji,
        desktopSize,
        tabletSize,
        mobileSize,
        color,
        darkColor,
        spacing
    } = attributes;

    const iconOptions = [
        {label: __('Arrow'), value: 'e958'},
        {label: __('Warning'), value: 'e981'},
        {label: __('Angle'), value: 'e926'},
        {label: __('Circle Arrow'), value: 'e902'},
        {label: __('Circle Checked'), value: 'e960'},
        {label: __('Plus'), value: 'e900'},
        {label: __('Minus'), value: 'e90e'},
        {label: __('Trend'), value: 'e954'},
        {label: __('Heart'), value: '6c'},
        {label: __('V'), value: 'e984'},
        {label: __('X'), value: 'e949'},
        {label: __('Ratio Checked'), value: 'ea54'},
        {label: __('Ratio Unchecked'), value: 'ea56'},
        {label: __('Star'), value: 'e951'},
        {label: __('Flash'), value: 'e986'},
        {label: __('Certification'), value: 'e983'},
    ];

    if (!icon) {
        setAttribute('icon', 'e958');
    }

    let wrapperClassName = 'list-style-element';

    if (emoji) {
        wrapperClassName += ' is-emoji';
    } else {
        wrapperClassName += ' is-icon';
    }

    const blockProps = useBlockProps({
        className: wrapperClassName,
        style: {
            ...(emoji ? {'--icon-code': `"${emoji}"`} : {'--icon-code': `"\\${icon}"`}),
            ...(desktopSize && {'--desktop-icon-size': `${desktopSize}px`}),
            ...(tabletSize && {'--tablet-icon-size': `${tabletSize}px`}),
            ...(mobileSize && {'--mobile-icon-size': `${mobileSize}px`}),
            ...(spacing && {'--item-spacing': `${spacing}px`}),
            ...(color && {'--icon-color': color}),
            ...(darkColor && {'--dark-icon-color': darkColor}),
        },
    });
    return (
        <div {...blockProps}>
            <InspectorControls>
                <PanelBody title={__('General')} initialOpen={true}>
                    <SelectControl
                        label={__('Icon')}
                        description={__('Select an icon for the list style.')}
                        value={icon}
                        options={iconOptions}
                        onChange={(value) => setAttribute('icon', value)}
                    />
                    <TextControl
                        label={__('or HTML Emoji Code')}
                        placeholder={__('&#10004;')}
                        value={emoji}
                        onChange={(value) => setAttribute('emoji', value)}
                        help={__('You can use the decimal (dec) code: w3schools.com/charsets/ref_emoji.asp')}
                    />
                </PanelBody>
                <PanelBody title={__('Style')} initialOpen={true}>
                    <label className="rb-control-label">{__('Icon Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="12"
                                value={desktopSize}
                                onChange={(value) => setAttribute('desktopSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="12"
                                value={tabletSize}
                                onChange={(value) => setAttribute('tabletSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                value={mobileSize}
                                onChange={(value) => setAttribute('mobileSize', value)}
                            />
                        </div>
                    </div>
                    <RangeControl
                        label={__('List Item Spacing')}
                        value={spacing}
                        onChange={(value) => setAttribute('spacing', value)}
                        step={1}
                        max={100}
                        min={0}
                        help={__('Select a spacing between li tags')}
                    />
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: color,
                                onChange: (value) => setAttribute('color', value),
                                label: __('Color'),
                            },
                            {
                                value: darkColor,
                                onChange: (value) => setAttribute('darkColor', value),
                                label: __('Dark Mode - Color'),
                            },
                        ]}
                    />
                </PanelBody>
            </InspectorControls>
            <InnerBlocks
                template={[
                    ['core/list', {placeholder: 'Enter list content...'}],
                ]}
                templateLock={true}
            />
        </div>
    )
}
